:root{
    --margin_text:40px;
}

.presentation-hello{
    font-weight: bold;
    font-size: 3.5rem;
    margin-top:30px;
    margin-bottom: 0;
    text-align: center;
    margin-right: var(--margin_text);
}

.presentation-description{
    font-weight: 400;
    font-size: 2rem;
    margin-bottom: 29px;
    text-align: center;
    margin-right: var(--margin_text);
}

@media (max-width:865px){
    .presentation-hello{
      
        margin-right: 0
    }
    
    .presentation-description{
       
        margin-right: 0
    }
}